import React, { useState, useEffect, useCallback } from "react";
import Container from "react-bootstrap/Container";
import { Jumbotron } from "./migration";
import Row from "react-bootstrap/Row";
import ProjectCard from "./ProjectCard";
import axios from "axios";
import GitHubCalendar from "./GithubCalendar";
import PersonalProjectCard from "./PersonalProjectCard";

const dummyProject = {
  name: null,
  description: null,
  svn_url: null,
  stargazers_count: null,
  languages_url: null,
  pushed_at: null,
};
const API = "https://api.github.com";
// const gitHubQuery = "/repos?sort=updated&direction=desc";
// const specficQuerry = "https://api.github.com/repos/hashirshoaeb/";

const Project = ({ heading, username, length, specfic, calendar, projects, biomedicalprojects }) => {
  const allReposAPI = `${API}/users/${username}/repos?sort=updated&direction=desc`;
  const specficReposAPI = `${API}/repos/${username}`;
  const dummyProjectsArr = new Array(length + specfic.length).fill(
    dummyProject
  );

  const [projectsArray, setProjectsArray] = useState([]);

  const fetchRepos = useCallback(async () => {
    let repoList = [];
    try {
      // getting all repos
      const response = await axios.get(allReposAPI);
      // slicing to the length
      // repoList = [...response.data.slice(0, length)]; // moved below
      // adding specified repos
      try {
        for (let repoName of specfic) {
          const response = await axios.get(`${specficReposAPI}/${repoName}`);
          repoList.push(response.data);
        }
      } catch (error) {
        console.error(error.message);
      }
      repoList = [...repoList, ...response.data.slice(0, length)]; // added
      // setting projectArray
      // TODO: remove the duplication.
      setProjectsArray(repoList);
    } catch (error) {
      console.error(error.message);
    }
  }, [allReposAPI, length, specfic, specficReposAPI]);

  useEffect(() => {
    fetchRepos();
  }, [fetchRepos]);

  return (
    <Jumbotron fluid id="projects" className="bg-light m-0">
      <Container className="">
        <h2 className="display-4 pb-5 text-center">{heading}</h2>
        <h3 className="display-6 pb-5 text-center">{projects.heading}</h3>
        <Row className="justify-content-center">
          {projects.data.length
            && projects.data.map((project, index) => (
            <PersonalProjectCard data={project}></PersonalProjectCard>
          ))}
        </Row>
        <h3 className="display-6 pb-5 text-center">{biomedicalprojects.heading}</h3>
        <Row className="justify-content-center">
          {biomedicalprojects.data.length
            && biomedicalprojects.data.map((project, index) => (
            <PersonalProjectCard data={project}></PersonalProjectCard>
          ))}
        </Row>
        <h3 className="display-6 pb-5 text-center" id="github">Recent Github Projects</h3>
        {calendar && (
          <Row className="justify-content-center pb-5">
            <GitHubCalendar username="itshady" />
          </Row>
        )}
        <Row>
          {projectsArray.length
            ? projectsArray.map((project, index) => (
              <ProjectCard
                key={`project-card-${index}`}
                id={`project-card-${index}`}
                value={project}
              />
            ))
            : dummyProjectsArr.map((project, index) => (
              <ProjectCard
                key={`dummy-${index}`}
                id={`dummy-${index}`}
                value={project}
              />
            ))}
        </Row>
      </Container>
    </Jumbotron>
  );
};

export default Project;
